<script>
import MainService from '../MainService';
import MobileNMService from '../MobileNMService';
import DesktopNMService from '../DesktopNMService';

export default {
	name: 'MainComponent',
	data() {
		return {
			certs: [],
			edu: [],
			exp: [],
			pers: [],
			skills: [],
			error: '',
      home() {
        if(window.innerWidth<768) {
          MobileNMService.scrollHome();
        } else if(window.innerWidth>=768){
          DesktopNMService.scrollHome();
        }
      },
      up() {
        if(window.innerWidth<768) {
          MobileNMService.scrollUp();
        } else if(window.innerWidth>=768){
          DesktopNMService.scrollUp();
        }
      },
      down() {
        if(window.innerWidth<768) {
          MobileNMService.scrollDown();
        } else if(window.innerWidth>=768){
          DesktopNMService.scrollDown();
        }
      }
		};
	},
	async created() {
		try {
			// Populate Data
			this.certs = await MainService.getCerts();
			this.edu = await MainService.getEdu();
			this.exp = await MainService.getExp();
			this.pers = await MainService.getPers();
			this.skills = await MainService.getSkills();
		} catch (err) {
			this.error = err;
		} // Populate Error
	},

};

MainService.initNavbar();
MainService.initDropdown();
MainService.listenForButtonHover();
</script>




