import JQuery from 'jquery';
const $ = JQuery;
const speed = 1000;
/**
	@class MobileNMService
				 Allows Navigation Menu JQuery methods for mobile devices.
				 Designed for dealing with devices up to a maximum width of 768px.

	@last_passed_testing 30/07/2020 
	@see DesktopNMService Desktop equivalent of this service.
*/
class MobileNMService {
	static scrollHome() {
			$('.nm-home > a').click(function() {
				console.log('LOADED => MOBILE')
				let $a = $('html, body').animate({
					scrollTop: $('.navbar').offset().top + 0
				}, speed);
				setTimeout(function(){
					$a.stop(true, false);
				}, speed);
			});
	}
	static scrollDown() {
		$('.nm-down > a').click(function() {
			let pos = $(document).scrollTop();
			let target;
			switch (true) {
				case (pos >= 0 && pos < 819):
					target = '.about';
					break;
				case (pos >= 819 && pos < 1587):
					target = '.education';
					break;
				case(pos >= 1587 && pos < 2355):
					target = '.certifications';
					break;
				case(pos >= 2355 && pos < 3123):
					target = '.experience';
					break;
				case(pos >= 3123 && pos < 3891):
					target = '.skills';
					break;
				case(pos >= 3891):
					target = '.contact';
					break;
			}
			try {
				// Animate
				let $a = $('html, body').animate({
					scrollTop: $(target).offset().top + 0
				}, speed);
				// Release scroll
				setTimeout(function(){
					$a.stop(true, false);
				}, speed);
			} catch (e) {
				console.log(e);
			}
		});
	}
	static scrollUp() {
		$('.nm-up > a').on('click', function(event) {
			let pos = $(document).scrollTop();
			let target;
			switch (true) {
				case (pos >= 0 && pos < 1587):
					target = '.navbar';
					break;
				case(pos >= 1587 && pos < 2356):
					target = '.about';
					break;
				case(pos >= 2356 && pos < 3124):
					target = '.education';
					break;
				case(pos >= 3124 && pos < 3892):
					target = '.certifications';
					break;
				case(pos >= 3892 && pos < 4756):
					target = '.experience';
					break;
				case(pos >= 4756 && pos < 4856):
					target = '.skills';
					break;
				case(pos >= 4856):
					target = '.contact';
					break;
			}
			try {
				// Animate
				let $a = $('html, body').animate({
					scrollTop: $(target).offset().top + 0
				}, speed);
				// Release scroll
				setTimeout(function(){
					$a.stop(true, false);
				}, speed);
			} catch (e) {
				console.log(e);
			}
		});
	}
} export default MobileNMService;