import JQuery from 'jquery';
const $ = JQuery;
const speed = 1000;
/**
	@class DesktopNMService
				 Allows Navigation Menu JQuery methods for desktop devices.
				 Designed for dealing with devices up to a maximum width of 1440px.

  @last_passed_testing 30/07/2020
	@see MobileNMService Mobile equivalent of this service.
*/
class DesktopNMService {
	static scrollHome() {
		console.log('LOADED => DESKTOP')
			$('.nm-home > a').click(function() {
				let $a = $('html, body').animate({
					scrollTop: $('.navbar').offset().top + 0
				}, speed);
				setTimeout(function(){
					$a.stop(true, false);
				}, speed);
			});
	}
	static scrollDown() {
		$('.nm-down > a').click(function() {
			let pos = $(document).scrollTop();
			let target;
			console.log(pos);
			switch (true) {
				case (pos >= 0 && pos < 819):
					target = '.about';
					break;
				case (pos >= 819 && pos < 1587):
					target = '.education';
					break;
				case(pos >= 1587 && pos < 2355):
					target = '.certifications';
					break;
				case(pos >= 2355 && pos < 3123):
					target = '.experience';
					break;
				case(pos >= 3123 && pos < 3891):
					target = '.skills';
					break;
				case(pos >= 3891 && pos < 4755):
					target = '.contact';
					break;
			}
			try {
				// Animate
				let $a = $('html, body').animate({
					scrollTop: $(target).offset().top + 0
				}, speed);
				// Release scroll
				setTimeout(function(){
					$a.stop(true, false);
				}, speed);
			} catch (e) {
				console.log(e);
			}
		});
	}
	static scrollUp() {
		$('.nm-up > a').on('click', function(event) {
			let pos = $(document).scrollTop();
			let target;
			console.log(pos);
			switch (true) {
				case (pos > 0 && pos <= 820):
					target = '.navbar';
					break;
				case(pos > 820 && pos <= 1588):
						target = '.about';
						break;
				case(pos > 1588 && pos <= 2365):
						target = '.education';
						break;
				case(pos > 2365 && pos <= 3124):
						target = '.certifications';
						break;
				case(pos > 3124 && pos <= 3892):
						target = '.experience';
						break;
				case(pos > 3892 && pos <= 4756):
					target = '.skills';
					break;
				case(pos > 4756):
					target = '.contact';
					break;
			}
			try {
				// Animate
				let $a = $('html, body').animate({
					scrollTop: $(target).offset().top + 0
				}, speed);
				// Release scroll
				setTimeout(function(){
					$a.stop(true, false);
				}, speed);
			} catch (e) {
				console.log(e);
			}
		});
	}
} export default DesktopNMService;